@import url(https://fonts.googleapis.com/css2?family=Poppins&family=Roboto&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#contact {
    padding: 7rem 0 6rem;
    overflow: hidden;
}
.sectionTitle {
    margin: 0.5rem 0 3rem;
    font-size: 2rem;
    font-weight: 700;
    position: relative;
    display: inline-block;
    padding-bottom: 0.6rem;
}
.sectionTitle::after {
    content: "";
    position: absolute;
    left: 0;
    bottom: -2px;
    width: 100px;
    border-radius: 70px;
    height: 4px;
    background: #7355F7;
}

.contactForm {
    padding: 1rem;
}
.contactForm input,
.contactForm textarea {
    width: 100%;
    margin: 0.5rem 0;
    padding: 1rem 0.8rem;
    border-radius: 0.3rem;
    border: 1px solid #bebebe;
    color: #000;
}
.contactForm input:focus,
.contactForm textarea:focus {
    outline: none !important;
    border: 1px solid #7054F2;
    background-color: rgb(243, 240, 255);
}
.contactForm textarea {
    height: 120px;
}
.ourValue {
    background: #7355F7;
    padding: 2rem;
}
.ourValueDetails {
    background-color: #fff;
    border-radius: 0.25rem;
    box-shadow: 0 0px 60px 0 rgb(0 0 0 / 10%);
    padding: 1.3rem 1rem 1rem;
    transition: all 1s;
    text-align: center;
}
.valueIcon {
    padding: 0.4rem 0.8rem;
    font-size: 3rem;
    color: #fff;
    border-radius: 0.25rem
} 
.valueIcon1 {
    background: #029e76;
    box-shadow: 0 2px 15px rgb(2 158 118 / 50%);
}
.valueIcon2 {
    box-shadow: 0 2px 15px rgb(255 168 8 / 50%);
    background-color: #ffa808;
}
.valueIcon3 {
    box-shadow: 0 2px 15px rgb(85 67 209 / 50%);
    background-color: #5543d1;
}
.valueIcon4 {
    box-shadow: 0 2px 15px rgb(255 88 110 / 50%);
    background-color: #ff586e;
}
.ourValueNumber {
    font-weight: 700;
    font-size: 2.4rem;
    color: #7355F7;
}
.ourValueTitle {
    color: #777777;
    margin: 1.5rem 0 1rem 0;
    font-weight: 500;
    font-size: 1.2rem;
}

@media (max-width: 981px) {
    .ourValueDetails{
        margin-top: 1rem;
    }
}
.navStyle {
    box-shadow: 0 2px 5px 1px rgb(64 60 67 / 16%);
    background: white;
    position: fixed!important;
    width: 100%;
    z-index: 99!important;
    transition: 0.5s!important;
    font-family: 'Poppins', sans-serif;
}
.navDefault {
    transition: 0.5s!important;
    padding: 25px 0 40px 0!important;
    margin-bottom: 2rem;
}

.mainNav .nav-link {
    color: #070120!important;
    margin: 0.5rem 1.2rem!important;
    font-weight: 500!important;
    transition: 0.3s;
    font-size: 1.1rem;
    font-family: 'Poppins', sans-serif;
}
.mainNav .nav-link:after {
    content: '';
    display: block;
    margin: auto;
    height: 3px;
    width: 0px;
    background: transparent;
    transition: width .5s ease, background-color .5s ease;
}
.mainNav .nav-link:hover:after {
    width: 100%;
    background: #4C25F5;
}

.navBrn {
    font-weight: 700;
    font-size: 1.5rem;
}
.brnIcon,
.navHighlight {
    color: #7456F7;;
}
.brnIcon {
    font-weight: 800;
    font-size: 1.6rem
}

.loginBtn {
    outline: none;
    border: none;
    border-radius: 50px;
    font-weight: 500;
    color:#fff;
    background: #7355F7;
    padding: 0.5rem 1.5rem;
    margin: 0.5rem 1rem;
    transition: 0.4s;
}
.loginBtn:hover { 
    background : #4B24F5
}
.navActiveClass{
    color:"red";
    font-weight:900;
}

@media (max-width: 981px) {
    .navDefault {
        background: white;
        padding: 0.8rem 0.5rem!important;
