.pricingNav {
    display: flex;
    justify-content: space-between;
    padding: 0 1rem;
}

.pricingNav img {
    width: 50%;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.pricingNav .nav-link {
    position: relative;
    height: 90px;
    width: 90px;
    border-radius: 50%;
    transition: 0.3s;
}

.priceLink1 > .nav-link {
    border: 7px solid #ffccd3;
}
.priceLink2 > .nav-link {
    border: 7px solid #a2ffd2;
}
.priceLink3 > .nav-link {
    border: 7px solid #fdd2ff;
}
.priceLink4 > .nav-link {
    border: 7px solid #CBEDFF;
}
.priceLink5 > .nav-link {
    border: 7px solid #dfdaff;
}
.priceLink6 > .nav-link {
    border: 7px solid #ffeb94;
}

.priceLink1 > a:hover, 
.priceLink1 > .nav-link.active {
    background: #FF4F66;
}
.priceLink2 > a:hover, 
.priceLink2 > .nav-link.active {
    background: #4ED797;

}
.priceLink3 > a:hover, 
.priceLink3 > .nav-link.active {
    background: #F78CFF;
}
.priceLink4 > a:hover, 
.priceLink4 > .nav-link.active {
    background: #76CDFF;
}
.priceLink5 > a:hover, 
.priceLink5 > .nav-link.active {
    background: #755BFF;
}
.priceLink6 > a:hover, 
.priceLink6 > .nav-link.active {
    background: #ffd30f;
}


