#dashboard {
    display: flex;
    width: 100%;
    min-height: 100vh;
    align-items: stretch;
    perspective: 1500px;
    background: #f4f7fc;
    font-family: 'Poppins', sans-serif;
  }
  
  #sidebar {
    min-width: 300px;
    max-width: 300px;
    height: 100vh;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    background: white;
    color: #878787;
    transition: all 0.6s cubic-bezier( 0.55, 0.055, 0.675, 0.19 ) ;
    padding-bottom: 0.5rem;
  }
  #sidebar.active {
    margin-left: -300px;
    transform: rotateY(100deg);
  }
  
  .sidebarContent {
    position: relative;
    height: 100%;
    width: 100%;
  }
  
  .backBtnBox {
    position: absolute;
    bottom: 0;
    padding: 0.2rem;
    width: 100%;
  }
  
  .backBtnBox .backBtn {
    display: block;
    width: 100%;
    text-transform: uppercase;
    background: #7254F6;
    border: none;
    font-size: 1rem;
    transition: 0.3s;
    padding: 0.4rem;
    font-weight: 500;
    color: #fff;
    border-radius: 0.3rem;
  }
  
  .backBtnBox .backBtn:hover{
    background: #4C25F5;
  }
  
  .backBtnBox a:hover {
    text-decoration: none;
  }
  
  #pageContent {
    width: 100%;
    min-height: 100vh;
    transition: all 0.3s;
    padding: 1rem;
  }
  
  .dashBoardHeader {
    display: flex;
    justify-content: space-between;
    padding: 0.6rem 2rem 0.6rem 1.3rem;
    margin-bottom: 1rem;
    background: #FFFFFF;
    align-items: center;
    border-radius: 0.2rem;
    box-shadow: 0 2px 5px 1px rgb(64 60 67 / 16%);
    transition: 0.4s;
    transform: button;
  }
  
  .dashBoardHeader h3 {
    font-size: 1.2rem;
    font-weight: 600;
    margin: 0;
    text-transform: uppercase;
  }
  
  .sideToggleBtn {
    width: 40px;
    height: 40px;
    background: #f5f5f5;
    cursor: pointer;
    outline: none;
    border: none;
  }
  
  /* Menu Hamburger Icon Animation */
  
  #nav-icon {
    width: 40px;
    height: 34px;
    position: relative;
    transform: rotate(0deg);
    transition: .7s ease-in-out;
    cursor: pointer;
    border-radius: 0.133rem;
    margin-right: 0.5rem;
    background: #eff2f7;
  }
  
  #nav-icon span {
    display: block;
    position: absolute;
    height: 3px;
    width: 50%;
    background: #696969;
    opacity: 1;
    transform: rotate(0deg);
    transition: .25s ease-in-out;
  }
  
  #nav-icon span:nth-child(even) {
    left: 35%;
    border-radius: 0 5px 5px 0;
  }
  
  #nav-icon span:nth-child(odd) {
    left: 0.4rem;
    border-radius: 5px 0 0 5px;
  }
  
  #nav-icon span:nth-child(1), #nav-icon span:nth-child(2) {
    top: 7px;
  }
  
  #nav-icon span:nth-child(3), #nav-icon span:nth-child(4) {
    top: 15px;
  }
  
  #nav-icon span:nth-child(5), #nav-icon span:nth-child(6) {
    top: 23px;
  }
  
  #nav-icon.open span:nth-child(1),#nav-icon.open span:nth-child(6) {
    transform: rotate(45deg);
  }
  
  #nav-icon.open span:nth-child(2),#nav-icon.open span:nth-child(5) {
    transform: rotate(-45deg);
  }
  
  #nav-icon.open span:nth-child(1) {
    left: 10px;
    top: 14px;
  }
  
  #nav-icon.open span:nth-child(2) {
    left: calc(50% - 10px);
    top: 14px;
  }
  
  #nav-icon.open span:nth-child(3) {
    left: -50%;
    opacity: 0;
  }
  
  #nav-icon.open span:nth-child(4) {
    left: 100%;
    opacity: 0;
  }
  
  #nav-icon.open span:nth-child(5) {
    left: 10px;
    top: 15px;
  }
  
  #nav-icon.open span:nth-child(6) {
    left: calc(50% - 10px);
    top: 15px;
  }
  
  .adminBtn {
    margin-top: 1.8rem;
    margin-left: -1rem;
  }
  
  .makeAdmin {
      background: #FFFFFF;
      border-radius: 1rem;
      padding: 3rem 2rem 8rem;
      margin-top: 2rem;
      box-shadow: 0 2px 5px 1px rgb(64 60 67 / 4%);
  }
  
.fContainer{
  position: relative;
  width: 100%;
  min-height: 100vh;
  overflow: hidden;
  font-family: 'Poppins', sans-serif;
}

.pageCloseBtn {
  position: absolute;
  top: 3px;
  right: 3px;
  cursor: pointer;
  height: 1.5rem;
  width: 1.5rem;
  border-radius: 50%;
  z-index: 33;
  color: #000;
  background: #F0F0F0;
  border: 1px solid #ACACAC;
  text-align: center;
  transition: 0.3s;
}
.pageCloseBtn:hover {
  background: #d6d6d6;;
}

.signInToast {
  position: absolute;
  top: 20%;
  right: 0;
  z-index: 123;
  background: #fff;
}

.signInToast .toastIcon {
  height: 1.2rem;
}

.fContainer::before {
  content: "";
  position: absolute;
  width: 2000px;
  height: 2000px;border-radius: 50%;
  background: linear-gradient(-45deg, #795cf9, #7355F7);
  top: -10%;
  right: 48%;
  transform: translateY(-50%);
  z-index: 6;
  transition: 1.8s ease-in-out;
}

.forms-container {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
}

.signIn-singUp {
  position: absolute;
  top: 50%;
  left: 75%;
  transform: translate(-50%, -50%);
  width: 50%;
  display: grid;
  grid-template-columns: 1fr;
  z-index: 5;
  transition: 1s 0.7s ease-in-out;
}

.fContainer form {
  display: flex;
  align-items: center;
  justify-content:center;
  flex-direction: column;
  overflow: hidden;
  grid-column: 1/2;
  grid-row: 1/2;
  transition: 0.2s 0.7s ease-in-out;
}

.fContainer form.sign-in-form {
  z-index: 2;
}

.fContainer form.sign-up-form {
  z-index: 1;
  opacity: 0;
}

.fContainer .title {
  font-size: 2.2rem;
  color: #444;
  margin-bottom: 1rem;
  font-weight: 600;
}

.input-field {
    max-width: 380px;
    width: 100%;
    background-color: #f0f0f0;
    margin: 10px 0;
    height: 55px;
    border-radius: 55px;
    display: grid;
    grid-template-columns: 15% 85%;
    padding: 0 0.4rem;
    position: relative;
}

.input-field .fIcon {
    text-align: center;
    line-height: 55px;
    color: #acacac;
    transition: 0.5s;
    font-size: 1.1rem;
}

.input-field input {
  padding-right: 0.5rem;
  background-color: #F0F0F0!important;
  border-radius: 55px;
  outline: none;
  border: none;
  line-height: 1;
  font-weight: 500;
  font-size: 1.1rem;
  color: #333;
  width: 100%;
}

.social-icon {
  height: 46px;
  width: 46px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0 0.45rem;
  color: #333;
  border-radius: 50%;
  border: 1px solid #333;
  background: #fff;
  outline: none;
  font-size: 1.1rem;
  transition: 0.3s;
  cursor: pointer;
}

.social-icon:hover {
  border: 1px solid #6e4dff;
  color: #6e4dff;
}

.input-field input::-webkit-input-placeholder {
  color: #aaa;
  font-weight: 500;
}

.input-field input:-ms-input-placeholder {
  color: #aaa;
  font-weight: 500;
}

.input-field input::placeholder {
  color: #aaa;
  font-weight: 500;
}

.iBtn {
  width: 160px;
  height: 49px;
  border: none;
  outline: none;
  border-radius: 50px;
  cursor: pointer;
  background-color: #7355F7;
  color: #fff;
  text-transform: uppercase;
  font-weight: 600;
  font-size: 1rem;
  margin: 10px 0;
  transition: .5s;
}
.iBtn:hover{
  background: #4C25F5;
}

.social-text {
  padding: 0.7rem 0;
  font-size: 1rem;
  margin-bottom: 0
}

.social-media {
  display: flex;
  justify-content: center;
}

.panels-container {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
}

.panel {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-around;
  text-align:center;
  z-index: 7;
}

.panel .content {
  color: #fff;
  transition: .9s .6s ease-in-out;
}

.panel h3 {
  font-weight: 600;
  line-height:1;
  font-size: 1.5rem;
}

.panel p {
  font-size: 0.95rem;
  padding: 0.7rem 0;
}

.iBtn.transparent {
  margin: 0;
  background:none;
  border: 2px solid #fff;
  width: 130px;
  height: 41px;
  font-weight: 600;
  font-size: 0.8rem;
}

.left-panel {
  pointer-events: all;
  padding: 3rem 17% 2rem 12%;
}

.right-panel {
  pointer-events: none;
  padding: 3rem 12% 2rem 17%;
}

.pImg {
  width: 100%;
  transition: 1.1s .4s ease-in-out;
}

.right-panel .content,
.right-panel .pImg {
  transform: translateX(800px);
}


/* Animation */

.fContainer.sign-up-mode::before{
  transform: translate(100%, -50%);
  right: 52%;
}

.fContainer.sign-up-mode .left-panel .pImg,
.fContainer.sign-up-mode .left-panel .content {
  transform: translateX(-800px)
}

.fContainer.sign-up-mode .right-panel .content,
.fContainer.sign-up-mode .right-panel .pImg {
  transform: translateX(0px)
}

.fContainer.sign-up-mode .left-panel {
  pointer-events: none;
}

.fContainer.sign-up-mode .right-panel {
  pointer-events:all;
}

.fContainer.sign-up-mode .signIn-singUp {
  left: 25%;
}

.fContainer.sign-up-mode form.sign-in-form {
  z-index: 1;
  opacity: 0;
}

.fContainer.sign-up-mode form.sign-up-form {
  z-index: 2;
  opacity: 1;
}

@media (max-width: 870px) {
  .fContainer {
    min-height: 800px;
    height: 100vh;
  }
  .signIn-singUp {
    width: 100%;
    top: 95%;
    transform: translate(-50%, -100%);
    transition: 1s 0.8s ease-in-out;
  }

  .signIn-singUp,
  .fContainer.sign-up-mode .signIn-singUp {
    left: 50%;
  }

  .panels-container {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr 2fr 1fr;
  }

  .panel {
    flex-direction: row;
    justify-content: space-around;
    align-items: center;
    padding: 2.5rem 8%;
    grid-column: 1 / 2;
  }

  .right-panel {
    grid-row: 3 / 4;
  }

  .left-panel {
    grid-row: 1 / 2;
  }

  .pImg {
    width: 200px;
    transition: transform 0.9s ease-in-out;
    transition-delay: 0.6s;
  }

  .panel .content {
    padding-right: 15%;
    transition: transform 0.9s ease-in-out;
    transition-delay: 0.8s;
  }

  .panel h3 {
    font-size: 1.2rem;
  }

  .panel p {
    font-size: 0.7rem;
    padding: 0.5rem 0;
  }

  .btn.transparent {
    width: 110px;
    height: 35px;
    font-size: 0.7rem;
  }

  .fContainer::before {
    width: 1500px;
    height: 1500px;
    transform: translateX(-50%);
    left: 30%;
    bottom: 68%;
    right: auto;
    right: initial;
    top: auto;
    top: initial;
    transition: 2s ease-in-out;
  }

  .fContainer.sign-up-mode:before {
    transform: translate(-50%, 100%);
    bottom: 32%;
    right: auto;
    right: initial;
  }

  .fContainer.sign-up-mode .left-panel .pImg,
  .fContainer.sign-up-mode .left-panel .content {
    transform: translateY(-300px);
  }

  .fContainer.sign-up-mode .right-panel .pImg,
  .fContainer.sign-up-mode .right-panel .content {
    transform: translateY(0px);
  }

  .right-panel .pImg,
  .right-panel .content {
    transform: translateY(300px);
  }

  .fContainer.sign-up-mode .signIn-singUp {
    top: 5%;
    transform: translate(-50%, 0);
  }
}

@media (max-width: 570px) {
  form {
    padding: 0 1.5rem;
  }

  .pImg {
    display: none;
  }
  .panel .content {
    padding: 0.5rem 1rem;
  }
  .fContainer {
    padding: 1.5rem;
  }

  .fContainer:before {
    bottom: 72%;
    left: 50%;
  }

  .fContainer.sign-up-mode::before {
    bottom: 28%;
    left: 50%;
  }
}


